@use 'global/variables' as v;
@use 'global/mixins' as m;

.card{
    max-width: 20.9rem;
    border: 1px solid v.$secondary-blue;
    border-radius: 1rem;
    @include m.tablet{
        max-width: 37rem;
    }
    .header-card{
        position: relative;
        img{
            border-radius: 1rem 1rem 0 0;
        }
        .icon-box{
            position: absolute;
            right: 0;
            top: 0;
            margin: 1.5rem;
            &.react{
                background-color: #86E1F9;
                border-radius: 0;
            }
        }
    }
    .body-card{
        background-color: v.$primary;
        padding: 2rem;
        border-radius: 0 0 1rem 1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        p{
            margin-top: 0;
            color: v.$secondary-blue;
        }
    }
    
}