@use 'global/mixins' as m;
@use 'global/variables' as v;

main {
    position: relative;

    .game {
        display: none;
    }

    @include m.tablet {
        display: flex;
        align-items: center;
        justify-content: center;

        .square {
            width: 3rem;
            height: 3.3rem;
        }

        .emptySquare {
            background-color: v.$primary-light;
        }

        .snakeSquare {
            background-color: v.$gradient-green;
        }

        .game {
            display: flex;
            position: relative;
            width: 60rem;
            height: 40rem;
            background: rgb(67, 217, 173);
            background: linear-gradient(135deg, rgba(67, 217, 173, 1) 0%, rgba(77, 91, 206, 1) 100%);
            border-radius: 1rem;

            .repeatGame {
                display: none;

                &.show {
                    display: flex;
                    position: absolute;
                    bottom: 0;
                    margin-left: 9.5rem;
                    margin-bottom: 6rem;
                }
            }

            .alert-game {
                display: none;

                &.show {
                    display: flex;
                    position: absolute;
                    bottom: 0;
                    margin-bottom: 10rem;
                    margin-left: 5rem;
                    background-color: v.$primary-light;
                    height: fit-content;
                    border-radius: 1rem;
                    border: 1px solid v.$primary-dark;
                }

                p {
                    font-size: 3rem;
                    color: v.$gradient-green;
                    padding: 2rem 4rem;
                    margin: 0;
                }
            }

            .circle {
                width: 1.5rem;
                height: 1.5rem;
                background: rgb(67, 217, 173);
                background: linear-gradient(180deg, rgba(67, 217, 173, 1) 0%, rgba(77, 91, 206, 1) 100%);
                position: absolute;
                margin: 1.5rem;
                border-radius: 50%;

                &:nth-child(1) {
                    left: 0;
                    top: 0;
                }

                &:nth-child(2) {
                    right: 0;
                    top: 0;
                }

                &:nth-child(3) {
                    right: 0;
                    bottom: 0;
                }

                &:nth-child(4) {
                    left: 0;
                    bottom: 0;
                }
            }

            .screen-game {
                position: absolute;
                display: grid;
                grid-template-columns: repeat(10, 1fr);
                margin-left: 3.5rem;
                margin-top: 3.5rem;
                border-radius: 1rem;
            }

            .startGame {
                display: none;
                &.show {
                    display: flex;
                    position: absolute;
                    margin-left: 12rem;
                    bottom: 0;
                    margin-bottom: 6rem;
                }
            }

            .tutorial {
                position: absolute;
                width: 20rem;
                height: 15rem;
                border-radius: 1rem;
                background-color: lighten($color: v.$primary-dark, $amount: 30);
                right: 0;
                margin-top: 3.5rem;
                margin-right: 3.5rem;

                p {
                    font-size: 1rem;
                    font-weight: normal;
                    text-align: center;
                }

                .arrows {
                    margin-right: 5rem;

                    .arrow {
                        position: absolute;
                        width: 6rem;
                        height: 3rem;
                        border-radius: 1rem;
                        background-color: v.$primary-dark;
                        display: flex;
                        justify-content: center;
                        align-items: center;

                        &:nth-child(1) {
                            margin-left: 7.2rem;
                        }

                        &:nth-child(2) {
                            margin-left: 13.3rem;
                            margin-top: 3.7rem;
                        }

                        &:nth-child(3) {
                            margin-left: 7.2rem;
                            margin-top: 3.7rem;
                        }

                        &:nth-child(4) {
                            margin-left: 1rem;
                            margin-top: 3.7rem;
                        }
                    }
                }
            }

            .left {
                position: absolute;
                margin-top: 18rem;
                margin-left: 37rem;

                .food-left {
                    display: flex;
                    justify-content: space-between;

                    .food {
                        width: 1.5rem;
                        height: 1.5rem;
                        border-radius: 50%;
                        background-color: v.$accent-green;
                        box-shadow: 0 0 1rem v.$gradient-green, 0 0 1rem v.$gradient-green, 0 0 1rem v.$gradient-green;
                    }
                }
            }
        }
    }
}

.colors {
    position: absolute;
    width: 100%;
    height: calc(100vh - 12rem);
    display: flex;
    justify-content: space-between;
    padding: 15rem;
    align-items: center;
    flex-direction: column;

    @include m.tablet {
        align-items: flex-end;
        padding-right: 30rem;
    }
}

.colors .color {
    filter: blur(8.5rem);
}

.colors .color:nth-child(1) {
    background: v.$accent-green;
    width: 10rem;
    height: 10rem;

    @include m.tablet {
        padding-right: 40rem;
    }
}

.colors .color:nth-child(2) {
    background: v.$secondary-purple;
    width: 15rem;
    height: 15rem;

    @include m.tablet {
        position: absolute;
        bottom: 0;
    }
}

.presentation {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 5rem;
    height: calc(100vh - 13rem);

    @include m.tablet {
        justify-content: center;
        height: calc(100vh - 12.5rem);
    }

    .iam {
        .hello {
            font-size: 1.8rem;
            font-weight: normal;
        }

        .myname {
            font-size: 6.2rem;
            font-weight: normal;
        }

        .developer {
            font-size: 2rem;
            color: v.$accent-green;
        }
    }

    .github {
        .github-profile {
            font-size: 1.4rem;
            color: v.$secondary-blue;
        }

        .const {
            font-size: 1.4rem;
            font-weight: normal;
            color: v.$secondary-purple;

            .githublink {
                color: v.$accent-green;
            }

            .link {
                color: v.$accent-red-light;
            }

            .equal {
                color: v.$secondary-white;
            }
        }
    }
}

.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid v.$secondary-blue;

    p {
        margin: 0;
        padding: 2rem;
        font-size: 1.4rem;
        font-weight: normal;
        color: v.$secondary-blue;

        @include m.tablet {
            border-right: 1px solid v.$secondary-blue;
        }
    }

    .icons-footer {
        a {
            padding: 2rem;
            border-left: 1px solid v.$secondary-blue;
        }
    }
}