@use 'global/mixins' as m;
@use 'global/variables' as v;

header {
    border-bottom: 1px solid v.$secondary-blue;
    z-index: 1;
    display: flex;
    flex-direction: column;

    .header {
        padding: 2rem;
        display: flex;
        justify-content: space-between;
    }

    .hamburger {
        width: 2.5rem;
        height: 2.5rem;
        background-image: url("../img/menu-line.svg");

        &.close {
            background-image: url("../img/close-line.svg");
        }

        @include m.tablet {
            display: none;
        }
    }

    h1 {
        color: v.$secondary-blue;
        font-weight: normal;
    }

    .hide {
        display: none;
    }

    .show {
        height: calc(100vh - 7rem);
        width: 100%;

        ul {
            padding: 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            margin: 0;

            li {
                border-bottom: 1px solid v.$secondary-blue;
                padding: 1.5rem;

                &:first-child {
                    border-top: 1px solid v.$secondary-blue;
                }
            }
        }
    }

    @include m.tablet {
        flex-direction: row;
        padding: 0;
        position: relative;
        .header{
            padding: 0;
        }
        .nav {
            display: flex;
            align-items: center;
            ul {
                margin: 0;
                display: flex;
                padding: 0;
                .left{
                    display: flex;
                    a{
                        &:last-child{
                            border-right: 1px solid v.$secondary-blue;
                            border-left: 0;
                        }
                    }
                }
                .right{
                    display: flex;
                    position: absolute;
                    right: 0;
                }
                a{
                    &.active{
                        color: v.$secondary-white;
                        border-bottom: 2px solid v.$accent-orange;
                    }
                }
            }
        }
        a{
            color: v.$secondary-blue;
            padding: 2rem;
            border-right: 1px solid v.$secondary-blue;
            &:last-child{
                border-right: 0;
                border-left: 1px solid v.$secondary-blue;
            }
            text-align: center;
            &:hover{
                color: v.$secondary-white;
            }
        }
    }
}

.nav {
    display: none;

    &.show {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
}