html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #011627;
  color: white;
  font-family: "Fira Code", monospace;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: white;
}

li {
  list-style: none;
}

img {
  max-width: 100%;
}

h1, h2 {
  font-family: "Fira Code", monospace;
  margin: 0;
  font-weight: bold;
  color: white;
}

p {
  color: white;
  font-size: 1.8rem;
}

h1 {
  font-size: 6.2rem;
}

h2 {
  font-size: 3.2rem;
}

.label {
  font-size: 1.6rem;
}

.code-snippet {
  font-size: 1.4rem;
}

.btn {
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 0.5rem;
  padding: 0.8rem 1rem;
  width: max-content;
}
.btn:hover {
  cursor: pointer;
}
.btn.btn-primary {
  background-color: #FEA55F;
  color: #01080E;
  border: none;
}
.btn.btn-primary:hover {
  background-color: #fec292;
}
.btn.btn-default {
  background-color: #1C2B3A;
  color: white;
  border: none;
}
.btn.btn-default:hover {
  background-color: #2d455c;
}
.btn.btn-ghost {
  background-color: transparent;
  border: 1px solid white;
  color: white;
}
.btn.btn-ghost:hover {
  border: 1px solid #e6e6e6;
}

.card {
  max-width: 20.9rem;
  border: 1px solid #607B96;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .card {
    max-width: 37rem;
  }
}
.card .header-card {
  position: relative;
}
.card .header-card img {
  border-radius: 1rem 1rem 0 0;
}
.card .header-card .icon-box {
  position: absolute;
  right: 0;
  top: 0;
  margin: 1.5rem;
}
.card .header-card .icon-box.react {
  background-color: #86E1F9;
  border-radius: 0;
}
.card .body-card {
  background-color: #011221;
  padding: 2rem;
  border-radius: 0 0 1rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card .body-card p {
  margin-top: 0;
  color: #607B96;
}

header {
  border-bottom: 1px solid #607B96;
  z-index: 1;
  display: flex;
  flex-direction: column;
}
header .header {
  padding: 2rem;
  display: flex;
  justify-content: space-between;
}
header .hamburger {
  width: 2.5rem;
  height: 2.5rem;
  background-image: url("../img/menu-line.svg");
}
header .hamburger.close {
  background-image: url("../img/close-line.svg");
}
@media (min-width: 768px) {
  header .hamburger {
    display: none;
  }
}
header h1 {
  color: #607B96;
  font-weight: normal;
}
header .hide {
  display: none;
}
header .show {
  height: calc(100vh - 7rem);
  width: 100%;
}
header .show ul {
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0;
}
header .show ul li {
  border-bottom: 1px solid #607B96;
  padding: 1.5rem;
}
header .show ul li:first-child {
  border-top: 1px solid #607B96;
}
@media (min-width: 768px) {
  header {
    flex-direction: row;
    padding: 0;
    position: relative;
  }
  header .header {
    padding: 0;
  }
  header .nav {
    display: flex;
    align-items: center;
  }
  header .nav ul {
    margin: 0;
    display: flex;
    padding: 0;
  }
  header .nav ul .left {
    display: flex;
  }
  header .nav ul .left a:last-child {
    border-right: 1px solid #607B96;
    border-left: 0;
  }
  header .nav ul .right {
    display: flex;
    position: absolute;
    right: 0;
  }
  header .nav ul a.active {
    color: white;
    border-bottom: 2px solid #FEA55F;
  }
  header a {
    color: #607B96;
    padding: 2rem;
    border-right: 1px solid #607B96;
    text-align: center;
  }
  header a:last-child {
    border-right: 0;
    border-left: 1px solid #607B96;
  }
  header a:hover {
    color: white;
  }
}

.nav {
  display: none;
}
.nav.show {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

main {
  position: relative;
}
main .game {
  display: none;
}
@media (min-width: 768px) {
  main {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  main .square {
    width: 3rem;
    height: 3.3rem;
  }
  main .emptySquare {
    background-color: #011627;
  }
  main .snakeSquare {
    background-color: #43D9AD;
  }
  main .game {
    display: flex;
    position: relative;
    width: 60rem;
    height: 40rem;
    background: rgb(67, 217, 173);
    background: linear-gradient(135deg, rgb(67, 217, 173) 0%, rgb(77, 91, 206) 100%);
    border-radius: 1rem;
  }
  main .game .repeatGame {
    display: none;
  }
  main .game .repeatGame.show {
    display: flex;
    position: absolute;
    bottom: 0;
    margin-left: 9.5rem;
    margin-bottom: 6rem;
  }
  main .game .alert-game {
    display: none;
  }
  main .game .alert-game.show {
    display: flex;
    position: absolute;
    bottom: 0;
    margin-bottom: 10rem;
    margin-left: 5rem;
    background-color: #011627;
    height: fit-content;
    border-radius: 1rem;
    border: 1px solid #01080E;
  }
  main .game .alert-game p {
    font-size: 3rem;
    color: #43D9AD;
    padding: 2rem 4rem;
    margin: 0;
  }
  main .game .circle {
    width: 1.5rem;
    height: 1.5rem;
    background: rgb(67, 217, 173);
    background: linear-gradient(180deg, rgb(67, 217, 173) 0%, rgb(77, 91, 206) 100%);
    position: absolute;
    margin: 1.5rem;
    border-radius: 50%;
  }
  main .game .circle:nth-child(1) {
    left: 0;
    top: 0;
  }
  main .game .circle:nth-child(2) {
    right: 0;
    top: 0;
  }
  main .game .circle:nth-child(3) {
    right: 0;
    bottom: 0;
  }
  main .game .circle:nth-child(4) {
    left: 0;
    bottom: 0;
  }
  main .game .screen-game {
    position: absolute;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    margin-left: 3.5rem;
    margin-top: 3.5rem;
    border-radius: 1rem;
  }
  main .game .startGame {
    display: none;
  }
  main .game .startGame.show {
    display: flex;
    position: absolute;
    margin-left: 12rem;
    bottom: 0;
    margin-bottom: 6rem;
  }
  main .game .tutorial {
    position: absolute;
    width: 20rem;
    height: 15rem;
    border-radius: 1rem;
    background-color: #0b5a9d;
    right: 0;
    margin-top: 3.5rem;
    margin-right: 3.5rem;
  }
  main .game .tutorial p {
    font-size: 1rem;
    font-weight: normal;
    text-align: center;
  }
  main .game .tutorial .arrows {
    margin-right: 5rem;
  }
  main .game .tutorial .arrows .arrow {
    position: absolute;
    width: 6rem;
    height: 3rem;
    border-radius: 1rem;
    background-color: #01080E;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  main .game .tutorial .arrows .arrow:nth-child(1) {
    margin-left: 7.2rem;
  }
  main .game .tutorial .arrows .arrow:nth-child(2) {
    margin-left: 13.3rem;
    margin-top: 3.7rem;
  }
  main .game .tutorial .arrows .arrow:nth-child(3) {
    margin-left: 7.2rem;
    margin-top: 3.7rem;
  }
  main .game .tutorial .arrows .arrow:nth-child(4) {
    margin-left: 1rem;
    margin-top: 3.7rem;
  }
  main .game .left {
    position: absolute;
    margin-top: 18rem;
    margin-left: 37rem;
  }
  main .game .left .food-left {
    display: flex;
    justify-content: space-between;
  }
  main .game .left .food-left .food {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    background-color: #43D9AD;
    box-shadow: 0 0 1rem #43D9AD, 0 0 1rem #43D9AD, 0 0 1rem #43D9AD;
  }
}

.colors {
  position: absolute;
  width: 100%;
  height: calc(100vh - 12rem);
  display: flex;
  justify-content: space-between;
  padding: 15rem;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .colors {
    align-items: flex-end;
    padding-right: 30rem;
  }
}

.colors .color {
  filter: blur(8.5rem);
}

.colors .color:nth-child(1) {
  background: #43D9AD;
  width: 10rem;
  height: 10rem;
}
@media (min-width: 768px) {
  .colors .color:nth-child(1) {
    padding-right: 40rem;
  }
}

.colors .color:nth-child(2) {
  background: #4D5BCE;
  width: 15rem;
  height: 15rem;
}
@media (min-width: 768px) {
  .colors .color:nth-child(2) {
    position: absolute;
    bottom: 0;
  }
}

.presentation {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5rem;
  height: calc(100vh - 13rem);
}
@media (min-width: 768px) {
  .presentation {
    justify-content: center;
    height: calc(100vh - 12.5rem);
  }
}
.presentation .iam .hello {
  font-size: 1.8rem;
  font-weight: normal;
}
.presentation .iam .myname {
  font-size: 6.2rem;
  font-weight: normal;
}
.presentation .iam .developer {
  font-size: 2rem;
  color: #43D9AD;
}
.presentation .github .github-profile {
  font-size: 1.4rem;
  color: #607B96;
}
.presentation .github .const {
  font-size: 1.4rem;
  font-weight: normal;
  color: #4D5BCE;
}
.presentation .github .const .githublink {
  color: #43D9AD;
}
.presentation .github .const .link {
  color: #E99287;
}
.presentation .github .const .equal {
  color: white;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #607B96;
}
.footer p {
  margin: 0;
  padding: 2rem;
  font-size: 1.4rem;
  font-weight: normal;
  color: #607B96;
}
@media (min-width: 768px) {
  .footer p {
    border-right: 1px solid #607B96;
  }
}
.footer .icons-footer a {
  padding: 2rem;
  border-left: 1px solid #607B96;
}