@use 'global/variables' as v;

.btn{
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    border-radius: .5rem;
    padding: .8rem 1rem;
    width: max-content;
    &:hover {
        cursor: pointer;
    }
    &.btn-primary{
        background-color: v.$accent-orange;
        color: v.$primary-dark;
        border: none;
        &:hover{
            background-color: lighten($color: v.$accent-orange, $amount: 10%);
        }
    }
    &.btn-default{
        background-color: v.$default;
        color: v.$secondary-white;
        border: none;
        &:hover{
            background-color: lighten($color: v.$default, $amount: 10%);
        }
    }
    &.btn-ghost{
        background-color: transparent;
        border: 1px solid v.$secondary-white;
        color: v.$secondary-white;
        &:hover{
            border: 1px solid darken($color: v.$secondary-white, $amount: 10%);
        }
    }
}