@use 'global/variables' as v;

h1,h2{
    font-family: v.$font-family;
    margin: 0;
    font-weight: bold;
    color: v.$secondary-white;
}
p{
    color: v.$secondary-white;
    font-size: 1.8rem;
}

h1{
    font-size: 6.2rem;
}
h2{
    font-size: 3.2rem;
}
.label{
    font-size: 1.6rem;
}
.code-snippet{
    font-size: 1.4rem;
}