@use 'global/mixins' as m;
@use 'global/variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    @include m.tablet{
        height: 100%;
    }
    display: flex;
    flex-direction: column;
    background-color: v.$primary-light;
    color: v.$secondary-white;
    font-family: v.$font-family;
    font-size: 1.6rem;
}
*, *:before, *:after {
    box-sizing: inherit;
}
a {
    text-decoration: none;
    color: v.$secondary-white;
}
li{
    list-style: none;
}
img {
    max-width: 100%;
}